.card{
    position: relative;
    background-color: white;
    width: 500px;
    padding: 15px;
    max-width: 75vw;
    height: 350px;
    box-shadow: 0px 18px 53px 0px rgba(0, 0, 0, 0.3);
    border-radius: 20px;
    background-size: cover;
    background-position: center;
}

.tinderCards__cardContainer{
    display: flex;
    justify-content: center;
    margin-top: 10vh ;
}

.swipe{
    position: absolute;
}

.cardContent{
    width: 100%;
    height: 100%;
}

.card h3{
    position: absolute;
    bottom: 0;
    margin: 10px;
    color: white;

}